@font-face {
    font-family: 'icon-font';
    src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}

.icon,
.icon-info-outline,
.icon-launch,
.icon-label,
.icon-chevron-left,
.icon-expand-more,
.icon-view,
.icon-component,
.icon-align-top,
.icon-align-bottom,
.icon-clear,
.icon-align-right,
.icon-align-left {
    font-family: 'icon-font';
    font-weight: normal;
    font-style: normal;
    line-height: normal;
    font-size: 1.4rem;
    vertical-align: sub; /*  ¯\_(ツ)_/¯ */
}

.icon-info-outline:before {
    content: '\e639';
}

.icon-launch:before {
    content: '\e648';
}

.icon-label:before {
    content: '\e887';
}


.icon-chevron-left:before {
    content: '\e8a7';
}

.icon-expand-more:before {
    content: '\e8ab';
}

.icon-view:before {
    content: '\e699';
}

.icon-component:before {
    content: '\e628';
}

.icon-align-top:before {
    content: '\e7a7';
}

.icon-clear:before {
